/* ----------------------------------------------------------------------------------
 * Bounce icon
 */
@media (max-width: 480px) {
  .title-card .bounce-icon {
    display: none; } }
.title-card .bounce-icon {
  -webkit-animation: bounce-icon 700ms linear infinite;
  -moz-animation: bounce-icon 700ms linear infinite;
  animation: bounce-icon 700ms linear infinite;
  position: absolute;
  bottom: 40px;
  left: 50%;
  margin-left: -5px; }
  .title-card .bounce-icon:before {
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    border-right: solid 1px white;
    border-bottom: solid 1px white;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg); }

@-webkit-keyframes bounce-icon {
  0% {
    -webkit-transform: translate3d(0, -10px, 0);
    opacity: 1; }

  80% {
    -webkit-transform: translate3d(0, 10px, 0);
    opacity: 0; }

  100% {
    -webkit-transform: translate3d(0, -10px, 0);
    opacity: 0; } }

@-moz-keyframes bounce-icon {
  0% {
    -moz-transform: translate3d(0, -10px, 0);
    opacity: 1; }

  80% {
    -moz-transform: translate3d(0, 10px, 0);
    opacity: 0; }

  100% {
    -moz-transform: translate3d(0, -10px, 0);
    opacity: 0; } }

@keyframes bounce-icon {
  0% {
    transform: translate3d(0, -10px, 0);
    opacity: 1; }

  80% {
    transform: translate3d(0, 10px, 0);
    opacity: 0; }

  100% {
    transform: translate3d(0, -10px, 0);
    opacity: 0; } }
